import request from '../utils/request'

// 
export function voiceList(data) {
    return request ({
        url: '/street/voice/list',
        method: 'POST',
        data
    })
}
// +1
export function voicePlay(data) {
    return request ({
        url: '/street/voice/play',
        method: 'POST',
        data
    })
}